<template>
   <el-form
        :model="form"
        label-width="110px"
        style="margin:20px;width:60%;min-width:600px;"
      >
        <el-form-item class="must_write" label=":">
          <el-input v-model="form.merchants_name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item class="must_write _fixmust" label="/:">
          <el-input v-model="form.org_name" checked placeholder="/"></el-input>
        </el-form-item>
        <el-form-item class="must_write" label=" ">
          <el-input v-model="form.brand" placeholder=""></el-input>
        </el-form-item>
        
           <el-form-item class="must_write" label="">
          <el-radio-group v-model="form.type">
            <el-radio :label="2"></el-radio>
            <el-radio :label="1"></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item class="must_write" label="">
          <el-radio-group v-model="form.edu_type">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
     
        <el-form-item v-if="form.edu_type===1" class="must_write _fixmust" label="">
          <el-radio-group v-model="form.org_type">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item v-if="form.edu_type===1" class="must_write _spin4" label=" ">
          <el-input v-model="form.org_code" placeholder=""></el-input>
        </el-form-item>

        <el-form-item v-if="form.edu_type===2" class="must_write _fixmust _spin" label=" ">
          <el-input v-model="form.brand_name" placeholder=""></el-input>
        </el-form-item>

        <el-form-item  class="must_write _spin4" label="">
          <el-col :span="12">
            <el-date-picker
              value-format="yyyyMMdd"
              format="yyyy  MM  dd "
              type="date"
              placeholder=""
              v-model="form.startime"
              style="width: 100%;"
            ></el-date-picker>
          </el-col>

          <el-col :span="12">
            <el-date-picker
              value-format="yyyyMMdd"
              format="yyyy  MM  dd "
              type="date"
              placeholder=""
              v-model="form.endtime"
              style="width: 100%;"
            ></el-date-picker>
          </el-col>
          <el-checkbox-group v-model="form.org_license_time">
            <el-checkbox label=""></el-checkbox>
          </el-checkbox-group>

        </el-form-item>
        <!--  -->
        <el-form-item class="must_write" label="">
          <el-upload
            tip=""
            limit="1"
            :headers="headers"
            name="media"
            action="http://192.168.202.190:8081/openApi/account_upload"
            list-type="picture-card"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove"
            :on-success="handleLicenseSuccess"
            :on-error="PicLicenseErr"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt />
          </el-dialog>
        </el-form-item>
        <el-form-item class="" label=":">
          <el-input v-model="form.legal_people_name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item class="must_write _spin3" label=":">
          <el-input v-model="form.owner_name" placeholder=""></el-input>
        </el-form-item>

        <el-form-item class="must_write _fixmust" label="">
          <el-row>
            <el-input
              style="width:80%"
              v-model="insertNumer.owner_phone"
              placeholder=""
            ></el-input>

            <el-button
              class="validateCode"
              @click="sendMsg()"
              :disabled="isDisabled"
            >{{buttonName}}</el-button>
          </el-row>
        </el-form-item>
        <el-form-item class="must_write must_write _spin3" label="">
          <el-row>
            <el-input style="width:80%" v-model="insertNumer.validateCode" placeholder=""
            ></el-input>
          </el-row>
        </el-form-item>

        <el-form-item class="must_write _spin3" label="">
          <el-input v-model="form.owner_mail" placeholder=""></el-input>
        </el-form-item>

        <el-form-item>
         
          <el-button @click.native.prevent="handleBack"></el-button>
        </el-form-item>
      </el-form>
</template>

<script>
import {phoneCode,createMerchants,checkMerchantState,getMerchantsInfo} from "../api/api";
export default {
  data() {
    return {
      disabled: true,
      buttonName: "",
      isDisabled: false,
      time: 10,
      insertNumer: {
        owner_phone: "",
        validateCode: ""
      },
      activeName: "first",
      checktimestate: "0",
      startime: "",
      endtime: "",
      form: {
        // 
        merchants_name: "",
        //
        org_name: "",
        brand: "",
        type:"",
        edu_type: "",
        //edutype1
        org_type: "",
        org_code: "",
        owner_name: "",
        owner_mail: "",
        legal_people_name:"",
        startime:"",
        endtime:"",
        org_license_time: "",
        //
        org_license:""
      },
      dialogImageUrl: "",
      dialogVisible: false
    };
  },
  computed: {
    headers() {
      return {
        ContentType: "multipart/form-data"
      };
    }
  },
  created(){
    if(this.$router){
    let params={
      merchants_id:sessionStorage.getItem("merchantsId")
    }
    getMerchantsInfo(params).then(data=>{
      console.log(data)
    })}
  },
  methods: {
    handleCreateMerchants() {
      if(sessionStorage.getItem("merchantsId")){
        this.$message({
          message:",",
          type:"warning"
        })
        return;
      }else{

      let credential={
        merchants_name:this.form.merchants_name,
        org_name: this.form.org_name,
        brand:this.form.brand,
        edu_type: this.form.edu_type,
        org_type: this.form.org_type,
        org_code: this.form.org_code,
        owner_name: this.form.owner_name,
        owner_mail: this.form.owner_mail,
        legal_people_name:this.form.legal_people_name,
        org_license_time: this.form.startime+this.form.endtime,
        org_license:this.org_license
      }
      let params={
        credential,
        type:this.form.type,
        phone:this.insertNumer.owner_phone,
        code:this.insertNumer.validateCode,
        name:sessionStorage.getItem("username")

      }
      createMerchants(params).then(data=>{
        if(!data.merchants_id){
            this.$message({
              message:"",
              type:"error"
            });
          return;
        }else{
        this.$message({
          message:"",
          type:"success"
        });
        sessionStorage.setItem("merchants_id",data.merchants_id)}
      })

      }
    },
    handleBack(){
      this.$router.push({path:"/submitInfoList"})
    },
    handleRemove(file, fileList) {
      //console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleLicenseSuccess(res, file) {
      this.form.org_license=res.data;
    },
    PicLicenseErr(err, file, fileList) {
      console.log(err);
    }
  }
};
</script>

<style lang="" >
.width_limit {
  min-width: 109px;
}
.el-form .el-form-item {
  position: relative;
}
.must_write::before {
  content: "*";
  font-size: 1em;
  color: red;
  position: absolute;
  margin: 12px 30px 0;
  z-index: 1;
}
._fixmust::before {
  margin: 12px 0px 0;
  z-index: 1;
}
._spin::before {
  margin-left: -8px;
}
._spin3::before {
  margin-left: 12px;
}
._spin4::before {
  margin-left: 46px;
}
</style>


